import React, { Fragment } from 'react';
import { NavLink } from 'react-router-dom';

import { elastic as Menu } from 'react-burger-menu';
import Routes from '../../Routes';

import styles from './Navigation.module.css';
import './Burger.css';

class Navigation extends React.Component {
    render() {
        let { trending, queue, watched } = Routes;
        let configuration = this.props.onConfiguration;

        if (this.props.onWidth < 768) {
            return (
                <Menu right>
                    <ul className={styles.wrapNav}>
                        <li className={styles.HeaderMenuHome}>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{ pathname: `${Routes.pageString}/${1}` }}
                            >
                                HOME
                            </NavLink>
                        </li>
                        <li className={styles.HeaderMenu}>
                            <NavLink to={trending}>My Libbary</NavLink>
                        </li>
                        <div className={styles.hiddenHeaderMenu}>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{
                                    pathname: watched,
                                    state: { configuration },
                                }}
                            >
                                Watched
                            </NavLink>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{
                                    pathname: queue,
                                    state: { configuration },
                                }}
                            >
                                Queue
                            </NavLink>
                        </div>
                    </ul>
                </Menu>
            );
        } else {
            return (
                <Fragment>
                    <ul className={styles.wrapNav}>
                        <li className={styles.HeaderMenuHome}>
                            <NavLink
                                exact
                                activeClassName={styles.menuIsActiv}
                                to={{ pathname: `${Routes.pageString}/${1}` }}
                            >
                                HOME
                            </NavLink>
                        </li>
                        <li className={styles.HeaderMenu}>
                            <a href="./">My Libbary</a>
                        </li>
                        <div className={styles.hiddenHeaderMenu}>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{
                                    pathname: watched,
                                    state: { configuration },
                                }}
                            >
                                Watched
                            </NavLink>
                            <NavLink
                                activeClassName={styles.menuIsActiv}
                                to={{
                                    pathname: queue,
                                    state: { configuration },
                                }}
                            >
                                Queue
                            </NavLink>
                        </div>
                    </ul>
                </Fragment>
            );
        }
    }
}

export default Navigation;
