import React, { Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import Routes from '../../Routes';
// import PropTypes from 'prop-types';
import stylesLogo from './Logo.module.css';

const Logo = () => {
    return (
        <Fragment>
            <NavLink
                className={stylesLogo.logoIMG}
                to={{ pathname: `${Routes.pageString}/${1}` }}
            >
                React <span className={stylesLogo.TV}> .TV</span>
            </NavLink>
        </Fragment>
    );
};

export default Logo;
