import React, { Component, Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';

import routes from './Routes';
import Queue from '../views/Queue';
import Trending from '../views/Trending';
import MovieDetails from '../views/MovieDetails';
import Watched from '../views/Watched';
import NotFound from '../views/NotFound';

import 'react-pagination-js/dist/styles.css';

//CSS========================

import 'normalize.css';

export default class App extends Component {
    render() {
        return (
            <Fragment>
                <Switch>
                    <Route path={routes.trending} exact component={Trending} />
                    <Route path={routes.page} component={Trending} />
                    <Route path={routes.movie} component={MovieDetails} />
                    <Route path={routes.queue} component={Queue} />
                    <Route path={routes.watched} component={Watched} />
                    <Route component={NotFound} />
                </Switch>
            </Fragment>
        );
    }
}
