import React from 'react';
import styles from './TrendingMovies.module.css';
import Routes from '../../Routes';
import requests from '../../../Requests/requests.json';
import { NavLink } from 'react-router-dom';

let TrendingMoviesItem = ({
    location,
    id,
    poster_path,
    title,
    vote_average,
    release_date,
    configuration,
    search,
}) => {
    poster_path = poster_path
        ? configuration.secure_base_url +
          //   configuration.poster_sizes[4] +
          'w500' +
          poster_path
        : requests.noImage;

    let LSQ = () => {
        if (location !== Routes.queue) {
            let titleArr = JSON.parse(localStorage.getItem('queue')) || [];
            let MoveID = titleArr.map(arr => arr.id).includes(id);
            if (!MoveID) {
                titleArr.push({
                    id,
                    poster_path,
                    title,
                    vote_average,
                    release_date,
                });
            }
            localStorage.setItem('queue', JSON.stringify(titleArr));
        } else if (id) {
            let titleArr = JSON.parse(localStorage.getItem('queue')) || [];
            titleArr.push({
                id,
                poster_path,
                title,
                vote_average,
                release_date,
            });
            localStorage.setItem(
                'queue',
                JSON.stringify(titleArr.filter(move => move.id !== id)),
            );
            window.location.reload();
        }
    };

    let LSWatched = () => {
        let titleArr = JSON.parse(localStorage.getItem('watched')) || [];
        let MoveID = titleArr.map(arr => arr.id).includes(id);
        if (!MoveID) {
            titleArr.push({
                id,
                poster_path,
                title,
                vote_average,
                release_date,
            });
        }
        localStorage.setItem('watched', JSON.stringify(titleArr));
    };

    return (
        <li className={styles.container}>
            <div className={styles.containerSVG}>
                <div className={styles.title}>
                    <NavLink
                        className={styles.imgSVGPlayBtn}
                        onClick={LSWatched}
                        to={{
                            pathname: `${Routes.movieString}/${id}`,
                            search: '',
                        }}
                    >
                        <svg
                            width="50pt"
                            height="50pt"
                            viewBox="0 0 50 50"
                            version="1.1"
                        >
                            <g id="surface1">
                                <path d="M 34.589844 24.160156 L 21.398438 15.136719 C 20.984375 14.835938 19.890625 14.835938 19.804688 15.976562 L 19.804688 34.023438 C 19.902344 35.164062 21.015625 35.183594 21.398438 34.863281 L 34.589844 25.839844 C 34.929688 25.632812 35.398438 24.796875 34.589844 24.160156 Z M 21.84375 32.101562 L 21.84375 17.898438 L 32.207031 25 Z M 21.84375 32.101562 " />
                                <path d="M 25 1.074219 C 11.804688 1.074219 1.074219 11.804688 1.074219 25 C 1.074219 38.195312 11.804688 48.925781 25 48.925781 C 38.195312 48.925781 48.925781 38.195312 48.925781 25 C 48.925781 11.804688 38.195312 1.074219 25 1.074219 Z M 25 46.882812 C 12.929688 46.882812 3.117188 37.070312 3.117188 25 C 3.117188 12.929688 12.929688 3.117188 25 3.117188 C 37.070312 3.117188 46.882812 12.929688 46.882812 25 C 46.882812 37.070312 37.070312 46.882812 25 46.882812 Z M 25 46.882812 " />
                            </g>
                        </svg>
                    </NavLink>
                </div>
                <button
                    className={
                        location !== Routes.watched
                            ? styles.btnForSvg
                            : styles.dNone
                    }
                    onClick={LSQ}
                >
                    <svg className={styles.imgSVG}>
                        <path d="M17 3H7c-1.1 0-2 .9-2 2v16l7-3 7 3V5c0-1.1-.9-2-2-2zm0 15l-5-2.18L7 18V6c0-.55.45-1 1-1h8c.55 0 1 .45 1 1v12z"></path>
                    </svg>
                </button>
                <div className={styles.contForSvgStar}>
                    <svg
                        className={styles.imgSVGStar}
                        id="Layer_1"
                        data-name="Layer 1"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 512 512"
                    >
                        <path d="M405.33,512a21.32,21.32,0,0,1-12.4-4L256,410.21,119.07,508A21.34,21.34,0,0,1,86,485.48l39.74-158.95L6.25,207.08a21.33,21.33,0,0,1,15.08-36.42H177.21L236,13.84a21.33,21.33,0,0,1,39.95,0l58.81,156.82H490.67a21.33,21.33,0,0,1,15.08,36.42L386.29,326.54,426,485.48A21.33,21.33,0,0,1,405.33,512ZM256,362.66a21.33,21.33,0,0,1,12.4,4l102.18,73L342,325.17a21.33,21.33,0,0,1,5.61-20.26l91.58-91.58H320a21.34,21.34,0,0,1-20-13.84L256,82.09,212,199.49a21.34,21.34,0,0,1-20,13.84H72.84l91.58,91.58A21.33,21.33,0,0,1,170,325.17L141.42,439.62l102.18-73A21.33,21.33,0,0,1,256,362.66Z" />
                    </svg>
                    <div className={styles.voteAverage}>{vote_average}</div>
                </div>
                <img className={styles.mainIMG} src={poster_path} alt={title} />
            </div>
        </li>
    );
};

export default TrendingMoviesItem;
